import React from "react";
import {
  Placeholder,
  Group,
  Avatar,
  CardGrid,
  Card,
  CellButton,
  PanelHeader,
  Panel,
  PromoBanner,
} from "@vkontakte/vkui";
import bridge from "@vkontakte/vk-bridge";
import { connect } from "react-redux";
import NowCounter from "./components/nowCounter";
import UserInfo from "./components/userInfo";
import IsMember from "./components/isMember";
import FavApp from "./components/favApp";
import FeedList from "./components/feedList";
import { motion } from "framer-motion";

class News extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      promo: null,
    };
  }

  componentDidMount() {
    window.scrollTo(0, 0);
    if (this.props.params) {
      this.setState({ isFav: Number(this.props.params.vk_is_favorite) });
    }
    fetch("https://mysterious-garden-57052.herokuapp.com/info/landing")
      .then((res) => res.json())
      .then((data) => {
        this.setState({
          countMasters: data.countMasters,
          countUsers: data.countUsers,
        });
      });
    bridge.subscribe((e) => {
      if (!e.detail) {
        return;
      }

      const { type, data } = e.detail;

      if (type === "VKWebAppGetAdsResult") {
        this.setState({ promo: data });
      }
    });
    bridge
      .send("VKWebAppGetAds", {})
      .then((data) => console.log("ads"))
      .catch((e) => console.log(e));
  }

  feedList = () => {
    return this.state.feed.map((feed) => {
      return (
        <CardGrid>
          <Card key={feed} size="l" mode="shadow">
            <div style={{ height: 96, backgroundColor: "aliceblue" }} />
          </Card>
        </CardGrid>
      );
    });
  };

  render() {
    const { user } = this.props;
    return (
      <Panel id="news">
        <PanelHeader></PanelHeader>
        <Group>
          <motion.div initial={{ x: -1000 }} animate={{ x: 0 }}>
            <Placeholder
              icon={
                <Avatar
                  src="https://sun1-28.userapi.com/O4KZM7zfdhZ-zHP-LtRj_xrYiNSRdraBcCQe6Q/PLqKmK-NWTY.jpg?ava=1"
                  size={70}
                />
              }
              header="!"
            >
                  -    . 
                   .
            </Placeholder>
          </motion.div>
          <UserInfo
            user={user}
            master={this.props.master}
            openReg={this.props.openReg}
          />
          <NowCounter
            countMasters={this.state.countMasters}
            countUsers={this.state.countUsers}
          />
          <IsMember params={this.props.params} />
          <FavApp isFav={+this.props.params.vk_is_favorite} />
          {/*<FeedList/>*/}
          {user.vkUid === "199500866" && (
            <CellButton onClick={this.props.openModer}></CellButton>
          )}
          {this.state.promo !== null ? (
            <Card size="l" mode="shadow">
              <PromoBanner
                bannerData={this.state.promo}
                onClose={() => this.setState({ promo: null })}
              />
            </Card>
          ) : null}
        </Group>
      </Panel>
    );
  }
}

const putStateToProps = (state) => {
  return {
    user: state.user,
    master: state.master,
    params: state.params,
  };
};

export default connect(putStateToProps)(News);
