import React, { useEffect, useState } from "react";
import { BACKEND } from "../../func/func";
import { Avatar, Banner, Button } from "@vkontakte/vkui";
import Icon56UserAddOutline from "@vkontakte/icons/dist/56/user_add_outline";
import bridge from "@vkontakte/vk-bridge";

const IsMember = ({ params }) => {
  const [inGroup, setInGroup] = useState(1);
  const joinGroup = () => {
    bridge
      .send("VKWebAppJoinGroup", {
        group_id: 193179174,
      })
      .then((res) => console.log(res))
      .catch((e) => console.log(e));
  };
  useEffect(() => {
    fetch(BACKEND.vkapi.isMember, {
      method: "POST",
      mode: "cors",
      cache: "no-cache",
      credentials: "same-origin",
      headers: { "Content-Type": "application/json" },
      redirect: "follow",
      referrer: "no-referrer",
      body: JSON.stringify({ params: params }),
    })
      .then((res) => res.json())
      .then((res) => setInGroup(res.ingroup));
  }, []);
  return !inGroup ? (
    <Banner
      before={
          <Avatar size={96}>
              <Icon56UserAddOutline fill={"#9fc6f3"} />
          </Avatar>
      }
      header="   !"
      subheader="        "
      actions={<Button onClick={joinGroup}></Button>}
    />
  ) : null;
};

export default IsMember;
