import React, { useEffect, useState } from "react";
import { Avatar, Banner, Button, Footer } from "@vkontakte/vkui";
import Icon56LikeOutline from "@vkontakte/icons/dist/56/like_outline";
import bridge from "@vkontakte/vk-bridge";

const FavApp = ({ isFav }) => {
    const [favStatus, setFavStatus] = useState(1)
  const addToFav = () => {
    bridge
      .send("VKWebAppAddToFavorites", {})
      .then((data) => {
        if (data.result === true) {
            setFavStatus(1)
        }
      })
      .catch((e) => console.log(e));
  };
    useEffect(()=>{
        setFavStatus(isFav)
    }, [])
  return !favStatus ? (
    <Banner
      before={
        <Avatar size={96}>
          <Icon56LikeOutline fill={"#9fc6f3"} />
        </Avatar>
      }
      subheader="       -      "
      actions={<Button onClick={addToFav}>  </Button>}
    />
  ) : (
    <Footer />
  );
};

export default FavApp;
